import React from 'react'
import { socket } from '../pages/Game'


const Player = () => {
    let [user, setUser] = React.useState({})
    let [score, setScore] = React.useState({})


    React.useEffect(() => {
        
        socket.emit('get_player')
        socket.on('get_player', data => {
            setUser(data);
        })
        socket.on('get_score', data => {
            setScore(data);
        })
    }, [])

    return (
        <div className='player__row'>
            <div className='player__info'>
                <p className='player'>Сыграно партий: <span>{score.all || 0}</span> </p>
            </div>
            <div className='player__info'>
                <p className='player'>{user.player_1 || 'Отсутствует'}: <span>{score.player_1_win || 0}</span> </p>
            </div>
            <div>
                <p className='player'>{user.player_2 || 'Отсутствует'}: <span>{score.player_2_win || 0}</span> </p>
            </div>
        </div>
    )
}

export default Player
