import React from 'react'
import Chat from '../component/Chat'
import TicTacToe from '../component/TicTacToe'
import {io} from 'socket.io-client'
export const socket = io("https://tic-tac-toe-game123.herokuapp.com/")

socket.on("connect", () => {});

const Game = () => {
    return (
        <div className='game'>
            <TicTacToe />
            <Chat />
        </div>
    )
}

export default Game
