import './App.css';
import {Route, Switch, Redirect } from 'react-router-dom'
import Autorization from './pages/Autorization';
import Game from './pages/Game';




function App() {

  return (
    <div className="App">
      <Switch>
        <Route path='/autorization' render={() => <Autorization />} />
        <Route exact path='/' render={() => <Game />} />
        <Redirect to='/' />
      </Switch>
      {!sessionStorage.getItem('player') && <Redirect to='/autorization' />}
    </div>
  );
}

export default App;
