import React from 'react'
import { socket } from '../pages/Game'

const GameArea = () => {
    let [gameBox, setGameBox] = React.useState([null, null, null, null, null, null, null, null, null])
    function set_figure(e) {
        if (e.target.getAttribute('data-number')) {
            socket.emit('set_position', {index: e.target.getAttribute('data-number')})
        }
    }
    React.useEffect(() => {
        socket.on('set_position', data => {
            setGameBox(data)
        })
    }, [])


    return (
        <div onClick={(e) => set_figure(e)} className='game__wrapper'>
            {gameBox.map((item, i) => {
                if (item == null)
                    return <div key={i} data-number={i + 1} className='game__box'></div>
                else 
                    return <div key={i} data-number={i + 1} className='game__box'>{item}</div>
            })}
        </div>
    )
}

export default GameArea
