import React from 'react'
import { socket } from '../pages/Game'

const Chat = () => {
    let [value, setValue] = React.useState('')
    let [message, setMessage] = React.useState([])

    React.useEffect(() =>{
        socket.on('send_message', data => {
            setMessage(prev => [...prev, data])
        })
    }, [])


    function send_message() {
        if (value) {
            socket.emit('send_message', {text: value, name: sessionStorage.getItem('player')})
        }
    }

    return (
        <div className='chat__wrapper'>
            <h2>Чат</h2>
            <div className='chat'>
                {message.map(item => {
                    return <div className="chat__message">
                                <p className='chat__from'>{item.name}</p>
                                <p className='chat__text'>{item.text}</p>
                            </div>
                })}
                
            </div>
            <div className='chat__send'>
                <input type="text" value={value} onChange={(e) => setValue(e.target.value)} />
                <button onClick={() => send_message()}>Отправить</button>
            </div>
        </div>
    )
}

export default Chat
